body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url('./resources/happy-birthday-decoration-cupcake-colorful-krem-den-rozhdeni.jpg') no-repeat center center fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.welcomeMessage{
  position: relative;
  color: #001f3f;
  padding-top: 200px;
}

.timer{
  position: absolute;
  color: white;
  padding-top: 200px;
  font-size: 50px;
}

.hiddenMessageDiv{
  transition: left 300ms linear;
}

.hiddenMessageButton{
  position: relative;
  padding-top: 100px;
}

.hiddenMessageReavealButton{
  font-size: 22px;
  size: 50px;
  border-radius: 5px;
  color:#CD853F;
  background-color: white;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer
}

.hiddenMessageReavealButton:hover{
  background-color: #CD853F;
  color: white;
  transform: scale(1.25);
  transition: 250ms;
}

#hiddenMessageParent{
  position: relative;
  padding-top: 100px;
}

a{
  color: white;
  font-size: 24px;
}

a[id="hiddenMessageParent"]:target ~ .theHiddenMessage{
  -webkit-transform: translateY( -500px);
  transform: translateY( -500px );
}

.theHiddenMessage{
  position: relative;
  padding-top: 500px;
  padding-bottom: 200px;
  transition: left 3000ms linear;
  color: #001f3f;
  font-size: 27px;
  font-weight: 450;
}
